body {
	background-color: #EFEFF4;
}
.adv {
	padding: 10px 0;
}
.adv img{
	width: 100%;
	height: 100px;
}
.news {
    width: 95%;
    height: 0.825rem;
    border-radius: 0.625rem;
		margin: 0.15625rem auto;
		padding: 0px 0.1875rem;
    background: #fff;
		display: table;
}
.newsfrom {
	display: table-cell;
	vertical-align: middle;
  text-align: center;
	width: 1.5rem;
	color: #f00;
	font-size: 0.328125rem;
}
.newsdes {
    font-size: 0.328125rem;
    padding: 0 0.1875rem;
    color: #f47979;
}
.newslist {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    overflow: auto;
    font-size: 0.28125rem;
    -ms-flex-item-align: start;
        align-self: flex-start;
    height: 0.825rem;
}
.newslist marquee {
	overflow: hidden;
}
.newslist ul li {
    height: 20px;
    line-height: 20px;
		list-style: none;
}
.newslist ul li a {
    color: #656565;
}
.morenews {
	display: table-cell;
	font-size: 0.28125rem;
	color: #040404;
	width: 1rem;
	vertical-align: middle;
  text-align: center;
}
.block {
	font-size: 0.28rem;
}
.block .header {
	padding: 5px;
}
.block .content {
	width: 100%;
	background: #fff;
	min-height: 20px;
}
.goodlist>li {
	min-height: 60px;
	padding: 12px 6px;
	border-bottom: 1px solid #dbdbdb;
	position: relative;
	list-style: none;
}
.goodlist>li .list-item {
	display: -webkit-box;
}
.goodlist>li .p {
	  display: inline-block;
    text-align: center;
    overflow: hidden;
		width: 75px;
    height: 75px;
    margin-right: 10px;
    margin-left: 8px;
}
.goodlist>li .d {
	margin: 4px 15px 6px 0;
	-webkit-box-flex: 1;
}
.goodlist .d .d-title {
	  line-height: 22px;
    margin-bottom: 3px;
    position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
    color: #ea5959;
		font-size: 0.382rem;
}
.mui-card {
	margin: 0;
	padding: 5px;
}
.mui-card-content {
	padding: 5px;
	text-align: center;
}
.charm-ranklist img {
	width: 2rem;
	height: 2rem;
	border-radius: 1rem;
}
.mui-card-footer {
	font-size: 0.21875rem;
}
.goods-list .price {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
.goods-list .price i {
	font-weight: 300;
  line-height: 0.7rem;
  font-size: 0.4rem;
}

